.grid-messages {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  &__item {
    flex: 1 1 100%;
    @include media-breakpoint-up('lg') {
      flex: 1 0 33.333%;
    }
  }

  &__item--left {
    margin-top: 1rem;
    margin-bottom: (30rem/16);
    @include media-breakpoint-up('lg') {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &__item--left &__message {
    @include media-breakpoint-up('lg') {
      &:first-child {
        border-bottom-right-radius: 0;
        &::before {
          bottom: 0;
          left: 100%;
          transform-origin: 0% 50%;
          transform: rotate(45deg);
        }
      }
  
      &:last-child {
        border-top-right-radius: 0;
        &::before {
          top: 0;
          left: 100%;
          transform-origin: 0% 50%;
          transform: rotate(-45deg);
        }
      }
    }
    @include media-breakpoint-up('xl')  {
      margin-right: 30px;
    }
  }

  &__item--center {
    background: none;
    flex: 0 1 auto;
    order: -1;
    text-align: center;
    margin: 30px 15px;
    @include media-breakpoint-up('sm') {
      margin: 40px 25px;
    }
    @include media-breakpoint-up('lg') {
      margin: 40px 0;
      order: 0;
      max-width: 33.333%;
    }
  }

  &__item--right {
    
  }

  &__item--right &__message {
    @include media-breakpoint-up('lg') {
      &:first-child {
        border-bottom-left-radius: 0;
        &::before {
          bottom: 0;
          right: 100%;
          transform-origin: 100% 50%;
          transform: rotate(-45deg);
        }
      }
  
      &:last-child {
        border-top-left-radius: 0;
        &::before {
          top: 0;
          right: 100%;
          transform-origin: 100% 50%;
          transform: rotate(45deg);
        }
      }
    }
    @include media-breakpoint-up('xl')  {
      margin-left: 30px;
    }
  }

  &__item--left, &__item--right {
    @include media-breakpoint-up('lg') {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &__message {
    border: 1px solid $color-text;
    border-radius: 10px;
    padding: 20px 15px;
    position: relative;
    &::before {
      @include media-breakpoint-up('lg') {
        content: '';
        position: absolute;
        height: 1px;
        background: $color-text;
        width: 60px;
      }
      @include media-breakpoint-up('xl')  {
        width: 100px;
      }
    }
  }

  &__message + &__message {
    margin-top: (30rem/16);
  }
}