.features {
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
  &__item {
    margin-bottom: 1rem;
    position: relative;
    min-height: 30px;
    transition: color $transition-menu;
    padding-left: 45px;
    color: $color-text-grey;
    display: flex;
    align-items: center;
    text-align: left;
    &::before {
      content: '';
      width: 30px;
      height: 30px;
      background: url('../img/arrow-feature.svg') no-repeat center center;
      background-size: contain;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transform: translate(-50%, 0);
      transition: transform $transition-menu, opacity $transition-menu;
      pointer-events: none;
    }
    &:hover {
      color: inherit;
      &::before {
        opacity: 1;
        transform: translate(0, 0);
      }
    }
  }
}