.tingle-modal * {
  box-sizing: border-box;
}
.tingle-modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  visibility: hidden;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  background: rgba(0, 0, 0, 0.9);
  opacity: 0;
}
@supports ((-webkit-backdrop-filter: blur(12px)) or (backdrop-filter: blur(12px))) {
  .tingle-modal {
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
  }
}
.tingle-modal--confirm .tingle-modal-box {
  text-align: center;
}
.tingle-modal--noOverlayClose {
  cursor: default;
}
.tingle-modal--noClose .tingle-modal__close {
  display: none;
}
.tingle-modal__close {
  position: relative;
  top: 19px;
  left: calc(50% - 30px);
  z-index: 1000;
  padding: 0;
  width: 16px;
  height: 16px;
  border: none;
  background-color: transparent;
  color: #666666;
  cursor: pointer;
  margin-top: auto;
  text-align: right;

  @media (min-width: 660px) {
    left: (315px - 15px);
  }

  & + .tingle-modal-box {
    margin-top: 0;
  }
}
.tingle-modal__close svg * {
  fill: #666666;
}
.tingle-modal__closeLabel {
  display: none;
}
.tingle-modal__close:hover {
  color: #666666;
}
.tingle-modal-box {
  position: relative;
  flex-shrink: 0;
  margin-top: auto;
  margin-bottom: auto;
  width: calc(100% - 30px);
  max-width: 630px;
  background: linear-gradient(360deg, #121212 0%, #242424 100%);
  background-color: #242424;
  opacity: 1;
  cursor: auto;
  will-change: transform, opacity;
  border-radius: 10px;
  @media (max-width: 540px) {
    font-size: 14px;
  }
  @media (max-width: 380px) {
    font-size: 12px;
  }
}
.tingle-modal-box__content {
  padding: (50rem/16) (36rem/16) (40rem/16);
}
.tingle-modal-box__footer {
  padding: 1.5rem 2rem;
  width: auto;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  // background-color: #f5f5f5;
  cursor: auto;
}
.tingle-modal-box__footer::after {
  display: table;
  clear: both;
  content: "";
}
.tingle-modal-box__footer--sticky {
  position: fixed;
  bottom: -200px;
  z-index: 10001;
  opacity: 1;
  transition: bottom 0.3s ease-in-out 0.3s;
}
.tingle-enabled {
  position: fixed;
  right: 0;
  left: 0;
  overflow: hidden;
}
.tingle-modal--visible .tingle-modal-box__footer {
  bottom: 0;
}
.tingle-modal--visible {
  visibility: visible;
  opacity: 1;
}
.tingle-modal--visible .tingle-modal-box {
  animation: scale 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
}
.tingle-modal--overflow {
  overflow-y: scroll;
  padding-top: 8vh;
}
.tingle-btn {
  display: inline-block;
  margin: 0 0.5rem;
  padding: 1rem 2rem;
  border: none;
  background-color: grey;
  box-shadow: none;
  color: #fff;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
  line-height: normal;
  cursor: pointer;
  transition: background-color 0.4s ease;
}
.tingle-btn--primary {
  background-color: #3498db;
}
.tingle-btn--danger {
  background-color: #e74c3c;
}
.tingle-btn--default {
  background-color: #34495e;
}
.tingle-btn--pull-left {
  float: left;
}
.tingle-btn--pull-right {
  float: right;
}
@media (max-width: 540px) {
  .tingle-modal {
      top: 0;
      // display: block;
      padding-top: 60px;
      width: 100%;
  }
  .tingle-modal-box {
      width: auto;
      border-radius: 0;
  }
  .tingle-modal-box__content {
    
  }
  .tingle-modal--noClose {
      top: 0;
  }
  .tingle-modal--noOverlayClose {
      padding-top: 0;
  }
  .tingle-modal-box__footer .tingle-btn {
      display: block;
      float: none;
      margin-bottom: 1rem;
      width: 100%;
  }
  .tingle-modal__close {
    left: calc(50% - 20px);
  }
  .tingle-modal__closeLabel {
     
  }
  .tingle-modal__closeIcon {
      // display: inline-block;
      // margin-right: 0.8rem;
      // width: 1.6rem;
      // vertical-align: middle;
      // font-size: 0;
  }
}
@keyframes scale {
  0% {
      opacity: 0;
      transform: scale(0.9);
  }
  100% {
      opacity: 1;
      transform: scale(1);
  }
}
